.gantt_task_content {
  color: #778CA2 !important;
}

.gantt_cell,
.gantt_cell_tree {
  width: 900px;
}
.gantt_tree_icon,
.gantt_file {
  display: none;
}

.gantt_task_row {
  display: none;
}

.gantt_line_wrapper {
  display: none;
}

.gantt_add {
  display: none;
}

.weekend {
  background: #ecfbf8 !important;
  color: #44d7b6 !important;
  font-weight: 800 !important;
}

.greenBorder .gantt_task_progress {
  background : linear-gradient(to right, #c5eee5 0%, #34A38B 100%);
  color: white;
}
.greenBorder {
  border-radius: 5px !important;
  border: #34A38B 3px solid !important;
  color: white !important;
}
.greenBorder:hover {
  border-left: #34A38B 10px solid !important;
  border-right: #34A38B 10px solid !important;
  color: white !important;
}
.greenBorder .gantt_task_content{
  color: white !important;
}
.pinkBorder {
  border-radius: 5px !important;
  border: #FD808B 3px solid !important;
  background-color: white !important;
}
.pinkBorder:hover {
  border-left: #FD808B 10px solid !important;
  border-right: #FD808B 10px solid !important;
}
.pinkBorder .gantt_task_progress {
  position: absolute;
  background: linear-gradient(to right,#faced1,  #FD808B)  !important;
  text-align: end;
  align-items: end;
}
.orangeBorder {
  border-radius: 5px !important;
  border:  #F8B520 3px solid !important;
  background-color: white !important;
}
.orangeBorder:hover {
  border-left: #F8B520 10px solid !important;
  border-right: #F8B520 10px solid !important;
}
.orangeBorder .gantt_task_progress {
  position: relative;
  background-color: white !important;
  text-align: right !important;
  align-items: right !important;
  align-content: right !important;
}
.gantt_task_progress {
  background: red;
}

.gantt_tree_content{
  font-weight: 900;
}
.bbb{
  display: none !important;
}
.gantt_task_progress_drag{display:none !important}
.gantt_hor_scroll{
  overflow: hidden !important;
}
.gantt_delete_button{
  background:red;
}

.link_cancel_btn{
  background-color: #34A38B  !important;
  background: #34A38B !important;
}

.gantt_popup_title ,.dhtmlx_popup_title{
  background-color: #ecfbf8 !important;
}
.gantt_popup_button ,.dhtmlx_popup_button ,.gantt_link_cancel_btn_button ,.dhtmlx_link_cancel_btn_button{
  background-color:#ecfbf8 !important;
}
