.left_nav li{
  color: #FFFFFF;
}
.soon{
color: var(--unnamed-color-ffffff);
text-align: left;
letter-spacing: 0px;
color: #FFFFFF !important;
opacity: 1;
background: transparent linear-gradient(270deg, #44D7B6 0%, #329A80 100%) 0% 0% no-repeat padding-box;
border-radius: 3px;
}
  .list_config{
    padding-top: 8vh;
  }
  ul{
    padding-left: 0;
  }
  li{
    cursor: pointer;
    padding-left: 4vh;
    line-height: 2.8; 
  }
  li p{
    line-height: 1.4 !important; 
  }

  .list_config li:hover,
  .li-back{
     background: url('../../../img/Mask1.svg');
     border-left:16px solid var(--light-light-blue); 
     left:2.5vh !important ;
     padding-left: 10px;
     /* border-left: 16px solid #44D7B6 !important;‚Äè */
}



.list_config li{
  overflow: auto;
}


.closeConfigurator{
     padding-left: 29px;
     position: absolute;
     width: 62px;
     height: 24px;
     margin-top: -34px;
     z-index: 99999;
     cursor: pointer;
 }

